import React, { useState, useEffect } from 'react';
import { Row, Col, message, Button } from 'antd';
import WebsiteCard from '../../components/WebsiteCard';
import AddWebsiteForm from '../../components/AddWebsiteForm';
import { GitHubService } from '../../services/github';
import { ReloadOutlined } from '@ant-design/icons';

const Home = () => {
  const [websites, setWebsites] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);

  useEffect(() => {
    loadWebsites();
  }, []);

  const loadWebsites = async () => {
    try {
      const data = await GitHubService.getWebsites();
      setWebsites(data);
    } catch (error) {
      message.error('加载网站列表失败');
    }
  };

  const handleDelete = async (id) => {
    try {
      await GitHubService.deleteWebsite(id);
      message.success('删除成功');
      loadWebsites();
    } catch (error) {
      message.error(error.message);
    }
  };

  const handleSubmit = async (values) => {
    try {
      await GitHubService.createWebsite(values);
      message.success('添加成功');
      setIsModalOpen(false);
      loadWebsites();
    } catch (error) {
      message.error(error.message);
    }
  };

  return (
    <>
      <div className="mb-4 flex justify-end">
        <Button 
          icon={<ReloadOutlined />}
          onClick={loadWebsites}
        >
          刷新
        </Button>
      </div>
      <Row gutter={[16, 16]}>
        {websites.map((website) => (
          <Col xs={24} sm={12} md={8} lg={6} key={website.id}>
            <WebsiteCard
              website={website}
              onDelete={() => handleDelete(website.id)}
            />
          </Col>
        ))}
      </Row>

      <AddWebsiteForm
        open={isModalOpen}
        onCancel={() => setIsModalOpen(false)}
        onSubmit={handleSubmit}
      />
    </>
  );
};

export default Home; 