import { AuthService } from './auth';

const REPO_OWNER = 'nbt0';
const REPO_NAME = 'personal-navigation';
const GITHUB_API = 'https://api.github.com';

export const GitHubService = {
  // 获取所有网站数据
  async getWebsites() {
    const response = await fetch(
      `${GITHUB_API}/repos/${REPO_OWNER}/${REPO_NAME}/issues?labels=website&state=open`
    );
    const issues = await response.json();
    return issues.map(issue => ({
      id: issue.number,
      title: issue.title,
      ...JSON.parse(issue.body)
    }));
  },

  // 创建网站（创建 Issue）
  async createWebsite(website) {
    const token = AuthService.getToken();
    if (!token) {
      throw new Error('需要先设置 GitHub Token');
    }

    const response = await fetch(
      `${GITHUB_API}/repos/${REPO_OWNER}/${REPO_NAME}/issues`,
      {
        method: 'POST',
        headers: {
          'Authorization': `token ${token}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          title: website.title,
          body: JSON.stringify({
            url: website.url,
            description: website.description,
            icon: website.icon
          }),
          labels: ['website']
        })
      }
    );

    if (!response.ok) {
      throw new Error('创建失败，请检查 Token 权限');
    }

    return response.json();
  },

  // 删除网站（关闭 Issue）
  async deleteWebsite(id) {
    const token = AuthService.getToken();
    if (!token) {
      throw new Error('需要先设置 GitHub Token');
    }

    const response = await fetch(
      `${GITHUB_API}/repos/${REPO_OWNER}/${REPO_NAME}/issues/${id}`,
      {
        method: 'PATCH',
        headers: {
          'Authorization': `token ${token}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          state: 'closed'
        })
      }
    );

    return response.ok;
  }
};
